import React, { PureComponent } from 'react';
import { useNavigate } from 'react-router-dom';

export const ConfirmEmail = () =>  {
    const navigator = useNavigate();
      
    return ( 
        <>
            <form action="" method='POST' onSubmit={(e) => e.preventDefault()}>
                <h1> E-mail</h1>
                <label htmlFor="">  ?</label>
                <div className="">
                    ""
                </div>
                <label htmlFor=""> </label>
                <div className="">
                    <input type="text" disabled/>
                </div>
                <div className="">
                    <button> </button>
                </div>
                <label htmlFor=""> </label>
                <div className="email-confirm flex-row">
                    <input type="text" />
                    <input type="text" />
                    <input type="text" />
                    <input type="text" />
                    <input type="text" />
                    <input type="text" />
                    <input type="text" />
                    <input type="text" />
                </div>

                <div className="">
                    <button></button>
                </div>
                                
                <span><a href="" onClick={(e) => {
                    e.preventDefault();
                    navigator("/register")
                }}></a></span>

            </form>
        </> 
    );
}

